import './Customize.css';
import React, { useState, useEffect, useRef, act } from 'react';
import { ChromePicker } from 'react-color';
import axios from 'axios';
import { useLocation } from 'react-router-dom';

// Component import
import RenderBoard from './component/RenderBoard1';
import RenderBoard_popup from './component/RenderBoard_popup';
import Banner from './component/Banner';
import Customize_Screen from './component/Customize_Screen';
import CustomizeBoardCircle from './component/CustomizeBoardCircle';
import Player from './component/Player';
import Circle from './component/Circle';
import Screen from './component/Screen';

import { ReactComponent as Play} from './svg/icon_play.svg';
import { ReactComponent as Stop} from './svg/icon_stop.svg';
import { ReactComponent as Pause} from './svg/icon_pause.svg';
import { ReactComponent as Plus} from './svg/icon_plus.svg';
import { ReactComponent as Trash} from './svg/icon_trash.svg';
import { ReactComponent as Add_Player} from './svg/icon-add-player.svg';
import { ReactComponent as Delete_Player} from './svg/icon_trash.svg';
import { ReactComponent as Player_Color} from './svg/icon-color.svg';
import { ReactComponent as Close} from './svg/icon_close.svg';
import { ReactComponent as Music} from './svg/icon_music.svg';
import { ReactComponent as Warn} from './svg/icon_warn.svg';

function Customize() {
    const [positionsHistory, setPositionsHistory] = useState([]);
    const [redoHistory, setRedoHistory] = useState([]);
    const [clickedPlayers, setClickedPlayers] = useState({});
    const [showColorPicker, setShowColorPicker] = useState(false);
    const [temporaryColor, setTemporaryColor] = useState('#fff');
    const [isUndoDisabled, setisUndoDisabled] = useState(true);
    const [isRedoDisabled, setisRedoDisabled] = useState(true);
    const [selectedScene, setSelectedScene] = useState(1); //   scene
    const [selectedSceneNumber, setSelectedSceneNumber] = useState(1); //   scene number
    const [handleScreen, sethandleScreen] = useState('Pause'); //   scene number
    const [selectedIcon, setSelectedIcon] = useState(null);
    const [playerUnactive, setPlayerUnactive] = useState(false);
    const [isWarning, setIsWarning] = useState(true);
    const [playerBoxes, setPlayerBoxes] = useState([]);
    const [storedPositions, setStoredPositions] = useState([]);
    const [DanceFormation, setDanceFormation] = useState([]);
    const [screenCount, setScreenCount] = useState(1);
    const [makeuptime, setMakeupTime] = useState([]);
    const [betweenTime, setBetweenTime] = useState([]);
    const [Name, setName] = useState("New Choreography");
    

    const [showPopup, setShowPopup] = useState(false);

    const colors = [
      '#FF5733', '#FF8C00', '#FFD700', '#ADFF2F', '#32CD32',
      '#00FA9A', '#20B2AA', '#1E90FF', '#4169E1', '#6A5ACD',
      '#9400D3', '#FF1493', '#FF4500', '#FFA500', '#FFFF00',
      '#7FFF00', '#00FF7F', '#40E0D0', '#4682B4', '#8A2BE2'
    ];


    const audioRef = useRef(null);


//// Local Storage ////
    // const location = useLocation();
    // const localStorageKey = `customizeData-${location.pathname}`;

    // //  
    // const [playerBoxes, setPlayerBoxes] = useState(() => {
    //     const savedData = localStorage.getItem(`${localStorageKey}-playerBoxes`);
    //     return savedData ? JSON.parse(savedData) : [];
    // });

    // const [storedPositions, setStoredPositions] = useState(() => {
    //     const savedData = localStorage.getItem(`${localStorageKey}-storedPositions`);
    //     return savedData ? JSON.parse(savedData) : [];
    // });

    // const [DanceFormation, setDanceFormation] = useState(() => {
    //     const savedData = localStorage.getItem(`${localStorageKey}-DanceFormation`);
    //     return savedData ? JSON.parse(savedData) : [];
    // });

    // const [screenCount, setScreenCount] = useState(() => {
    //     const savedData = localStorage.getItem(`${localStorageKey}-screenCount`);
    //     return savedData ? JSON.parse(savedData) : 1;
    // });

    // const [makeuptime, setMakeupTime] = useState(() => {
    //     const savedData = localStorage.getItem(`${localStorageKey}-makeuptime`);
    //     return savedData ? JSON.parse(savedData) : [];
    // });

    // const [betweenTime, setBetweenTime] = useState(() => {
    //     const savedData = localStorage.getItem(`${localStorageKey}-betweenTime`);
    //     return savedData ? JSON.parse(savedData) : [];
    // });

    // const [Name, setName] = useState(() => {
    //     return localStorage.getItem(`${localStorageKey}-Name`) || "New Choreography";
    // });

    // //      
    // useEffect(() => {
    //     localStorage.setItem(`${localStorageKey}-playerBoxes`, JSON.stringify(playerBoxes));
    // }, [playerBoxes, localStorageKey]);

    // useEffect(() => {
    //     localStorage.setItem(`${localStorageKey}-storedPositions`, JSON.stringify(storedPositions));
    // }, [storedPositions, localStorageKey]);

    // useEffect(() => {
    //     localStorage.setItem(`${localStorageKey}-DanceFormation`, JSON.stringify(DanceFormation));
    // }, [DanceFormation, localStorageKey]);

    // useEffect(() => {
    //     localStorage.setItem(`${localStorageKey}-screenCount`, JSON.stringify(screenCount));
    // }, [screenCount, localStorageKey]);

    // useEffect(() => {
    //     localStorage.setItem(`${localStorageKey}-makeuptime`, JSON.stringify(makeuptime));
    // }, [makeuptime, localStorageKey]);

    // useEffect(() => {
    //     localStorage.setItem(`${localStorageKey}-betweenTime`, JSON.stringify(betweenTime));
    // }, [betweenTime, localStorageKey]);

    // useEffect(() => {
    //     localStorage.setItem(`${localStorageKey}-Name`, Name);
    // }, [Name, localStorageKey]);


///// Click, Add, Delete Player /////
    const handlePlayerClick = (index) => {
        setClickedPlayers((prevClickedPlayers) => {
          const updatedClickedPlayers = { ...prevClickedPlayers };
          updatedClickedPlayers[index] = !updatedClickedPlayers[index];
          return updatedClickedPlayers;
        });
      }

    const isAnyPlayerClicked = Object.values(clickedPlayers).some((clicked) => clicked);


    const handleAddPlayer = () => {
      const nextPlayerNumber = playerBoxes.length > 0 
      ? Math.max(...playerBoxes.map(box => box.number)) + 1 
      : 1;
      const usedColors = playerBoxes.map(box => box.color);
      const availableColor = colors.find(color => !usedColors.includes(color)) || colors[nextPlayerNumber % colors.length];
      const defaultPosition = {
        x: 10 + (((nextPlayerNumber-1) % 12) * 80),
        y: 10 + (Math.floor((nextPlayerNumber-1) / 12) * 80),
        color: availableColor,
      };
      const newPlayerBox = { id: Date.now(), number: nextPlayerNumber, color: availableColor, position: defaultPosition};
      setPlayerBoxes((prevBoxes) => [...prevBoxes, newPlayerBox]);
      setRedoHistory([]); //    redo  

    };


    const handleDeletePlayer = () => {
      const deletedPlayers = {};
    
      const updatedPlayerBoxes = playerBoxes.filter((box, index) => {
        if (clickedPlayers[index]) {
          deletedPlayers[index] = box;
          return false; //   
        }
        return true; //   
      });
      setPlayerBoxes(updatedPlayerBoxes);
      setClickedPlayers({});
      setRedoHistory([]); //    redo  
    };
    
    const handleDragOver = (e) => {
      e.preventDefault(); //  
    };


///// Player Color /////
    const handlePlayerColor = () => {
        if (Object.values(clickedPlayers).some((clicked) => clicked)) {
          setShowColorPicker(true);
        }
      };

    const handleColorChange = (color) => {
        setTemporaryColor(color.hex);
    };

    const confirmColorChange = () => {
        setPlayerBoxes((prevBoxes) =>
          prevBoxes.map((box, index) =>
            clickedPlayers[index] ? { ...box, color: temporaryColor } : box
          )
        );
        setShowColorPicker(false);
        setClickedPlayers({});
      };

    const cancelColorChange = () => {
        setShowColorPicker(false);
    };



///// Add & Remove & Click Screen /////
    const handleAddScreen = () => {
      setScreenCount((prev) => prev + 1);
      setPlayerUnactive(true);
    };
    
    const [audioFile, setAudioFile] = useState(null); //    
    const [audioUrl, setAudioUrl] = useState(null); //   URL
    const fileInputRef = useRef(null);


    //     
    const handleAudioChange = (event) => {
        setPlayerUnactive(true);
        const file = event.target.files[0];
        if (file && file.type.startsWith('audio/')) {
            setAudioFile(file);
            setAudioUrl(URL.createObjectURL(file)); //    URL 
        } else {
            alert('  .');
        }
    };

    const handleIconClick = () => {
        fileInputRef.current.click();
    };

    const handleRemoveScreen = () => {
      if (screenCount <= 1) {
          alert("    .");
          return;
      }
  
      setPlayerUnactive(true);
      setScreenCount((prev) => prev - 1);
  
      //   DanceFormation 
      setDanceFormation((prevFormation) => {
          const updatedFormation = [...prevFormation];
          updatedFormation.splice(selectedScene - 1, 1); //   scene 
          return updatedFormation;
      });

  
      //   storedPositions 
      setStoredPositions((prevStoredPositions) => {
          const updatedStoredPositions = [...prevStoredPositions];
          updatedStoredPositions.splice(selectedScene - 1, 1); //   scene 
          return updatedStoredPositions;
      });
  
      //   makeuptime 
      setMakeupTime((prevMakeupTime) => {
          const updatedMakeupTime = [...prevMakeupTime];
          updatedMakeupTime.splice(selectedScene - 1, 1); //   scene 
          return updatedMakeupTime;
      });
  
      //   betweentime 
      setBetweenTime((prevBetweenTime) => {
          const updatedBetweenTime = [...prevBetweenTime];
          updatedBetweenTime.splice(selectedScene - 1, 1); //   scene 
          return updatedBetweenTime;
      });
  
      //     
      setSelectedScene((prev) => Math.max(1, prev - 1));
    };
  

    const handleScreenClick = (index) => {
      setSelectedSceneNumber(index);
    }






///// Save & Update Positions /////
    const handleSavePositions = (savedPositions) => {
      setStoredPositions((prevStoredPositions) => {
        const updatedStoredPositions = [...prevStoredPositions];
        updatedStoredPositions[selectedScene - 1] = savedPositions;
        return updatedStoredPositions;
      });
    };

    const StoredDanceFormation = (newDanceFormation) => {
      setDanceFormation((prevFormation) => {
          if (JSON.stringify(prevFormation) !== JSON.stringify(newDanceFormation)) {
              return newDanceFormation; // Update only if different
          }
          return prevFormation; // Return previous state if no change
      });
  };
  

    const DragupdatePosition = (index, newX, newY) => {
      
      setStoredPositions((prevStoredPositions) => {
        const updatedStoredPositions = [...prevStoredPositions];
        if (!updatedStoredPositions[selectedScene - 1]) updatedStoredPositions[selectedScene - 1] = [];
        updatedStoredPositions[selectedScene - 1][index] = {
          ...updatedStoredPositions[selectedScene - 1][index],
          x: newX,
          y: newY,
        };
    
        return updatedStoredPositions;
      });
    
      // move  history 
      setPositionsHistory((prevHistory) => {
        const updatedHistory = [...prevHistory];
        updatedHistory.push({
          positions: [...storedPositions[selectedScene - 1]],
          playerBoxes: [...playerBoxes],
          action: 'move',
        });
    
        return updatedHistory;
      });
    
      setisUndoDisabled(false); // undo 
    };
    


///// UseEffect /////
    useEffect(() => {
      if (!showPopup) { // Only execute if showPopup is false
        console.log('Update stored positions');
          setStoredPositions((prevStoredPositions) => {
              const updatedStoredPositions = [...prevStoredPositions];
              updatedStoredPositions[selectedScene - 1] = playerBoxes.map((player, index) => {
                  if (prevStoredPositions[selectedScene - 1] && prevStoredPositions[selectedScene - 1][index]) {
                      return prevStoredPositions[selectedScene - 1][index];
                  } else {
                      return {
                          x: 10 + ((index % 12) * 80),
                          y: 10 + (Math.floor(index / 12) * 80),
                          color: player.color,
                      };
                  }
              });
              return updatedStoredPositions;
          });
      }
    }, [playerBoxes, showPopup]); // Add showPopup as a dependency


    useEffect(() => {
      setPositionsHistory((prevHistory) => {
          const updatedHistory = [...prevHistory];
  
          // storedPositions[selectedScene - 1]  
          if (playerBoxes.length > 0 && Array.isArray(storedPositions[selectedScene - 1])) {
              updatedHistory.push({
                  positions: [...storedPositions[selectedScene - 1]], //   
                  playerBoxes: [...playerBoxes],
                  action: 'Reset',
              });
          }
          
          return updatedHistory;
      });
  }, [storedPositions, playerBoxes, selectedScene]); // selectedScene  
  

    
    

///// Undo & Redo /////
    const handleUndo = () => {
      if (positionsHistory[selectedScene - 1] && positionsHistory[selectedScene - 1].length > 0) {

      }
    };
    
    const handleRedo = () => {
      if (redoHistory[selectedScene - 1] && redoHistory[selectedScene - 1].length > 0) {

      }
    };
    
    const handlePlay = () => {
      console.log('Play');
      setShowPopup(true);
      sethandleScreen('Stop');
      
      // audioRef    pause() 
      if (audioRef.current) {
          audioRef.current.pause(); //     
          audioRef.current.currentTime = 0; //   
      }
    };

    const handleClosePopup = () => {
        sethandleScreen('Stop');
        setShowPopup(false);
        
        // audioRef    pause() 
        if (audioRef.current) {
            audioRef.current.pause(); //     
            audioRef.current.currentTime = 0; //   
        }
    };

    const handleClickScreen = (action) => {
        console.log('action', action);
        setSelectedIcon(action);
        sethandleScreen(action);

        // action   
        if (audioRef.current) {
            switch (action) {
                case 'Play':
                    audioRef.current.play(); //  
                    break;
                case 'Pause':
                    audioRef.current.pause(); //  
                    break;
                case 'Stop':
                    audioRef.current.pause(); //  
                    audioRef.current.currentTime = 0; //  
                    break;
                default:
                    break;
            }
        }
    };

    const setSceneTimes = (newSceneTimes) => {
      const newSceneTimesCopy = [...newSceneTimes];
      setMakeupTime(newSceneTimesCopy);
    }

    const setMovingTimes = (newMovingTimes) => {
      const newMovingTimesCopy = [...newMovingTimes];
      setBetweenTime(newMovingTimesCopy);
    }

  return (
    <div>

      <div className="Customize">
        <Banner onUndo={handleUndo} onRedo={handleRedo} onPlay={handlePlay} onName={setName} L_position="1752px" isUndoDisabled={isUndoDisabled} isRedoDisabled={isRedoDisabled} playerBoxes={playerBoxes} />

        <div className="Customize_Side_Menu">
            <div className="Customize_Side_Menu_Player_Box">
                <div className="Customize_Side_Menu_Player_Title">Players</div>
                {playerBoxes.map((box, index) => (
                    <Player
                        key={box.id} //  ID 
                        number={box.number} //  player number 
                        color={box.color}
                        isClicked={clickedPlayers[index] || false}
                        onClick={() => handlePlayerClick(index)}
                    />
                ))}
            </div>

            <div className="Customize_Side_Menu_Player_manuplate">
              <span 
                className='Customize_Side_Menu_Player_manuplate_icon_box' 
                onClick={!playerUnactive && playerBoxes.length < 24 ? handleAddPlayer : undefined} 
                style={{ 
                    pointerEvents: playerUnactive || playerBoxes.length >= 24 ? 'none' : 'auto', 
                    opacity: playerUnactive || playerBoxes.length >= 24 ? 0.4 : 1 
                }}
              > 
                  <Add_Player />
              </span>

              <span 
                  className='Customize_Side_Menu_Player_manuplate_icon_box' 
                  onClick={!playerUnactive && isAnyPlayerClicked ? handleDeletePlayer : undefined}
                  style={{ 
                      pointerEvents: playerUnactive || !isAnyPlayerClicked ? 'none' : 'auto', 
                      opacity: playerUnactive || !isAnyPlayerClicked ? 0.4 : 1 
                  }}      
              > 
                  <Delete_Player />
              </span>

              <span 
                  className='Customize_Side_Menu_Player_manuplate_icon_box' 
                  onClick={!playerUnactive && isAnyPlayerClicked ? handlePlayerColor : undefined}
                  style={{ 
                      pointerEvents: playerUnactive || !isAnyPlayerClicked ? 'none' : 'auto', 
                      opacity: playerUnactive || !isAnyPlayerClicked ? 0.4 : 1 
                  }}      
              > 
                  <Player_Color />
              </span>

            </div>
        </div>

        {showColorPicker && (
            <>
                <div className="color-picker-overlay" onClick={cancelColorChange} />
                <div className="color-picker-container">
                    <ChromePicker color={temporaryColor} onChange={handleColorChange} />
                    <div className="color-picker-buttons">
                        <button onClick={confirmColorChange}>Confirm</button>
                        <button onClick={cancelColorChange}>Cancel</button>
                    </div>
                </div>
            </>
        )}
        <div className='Customize_Warning'><Warn /></div>
        <div className='Customize_Down_Menu_icon_frame'>
            <span className='Customize_Down_Menu_icon_box' onClick={handleAddScreen}><Plus /></span>
            <span className='Customize_Down_Menu_icon_box' onClick={handleRemoveScreen}><Trash /></span>
            <span className='Customize_Down_Menu_icon_box' onClick={handleIconClick}>
                <Music />

            </span>
            <input
                type="file"
                accept="audio/*"
                ref={fileInputRef}
                style={{ display: 'none' }} // input  
                onChange={handleAudioChange}
            />
            {audioUrl && (
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: '15px', marginTop: '0px' }}>
                    {/* <p style={{ margin: 0, color: '#ffffff', whiteSpace: 'nowrap' }}>
                       : {audioFile.name}
                    </p> */}
                    <audio controls src={audioUrl} style={{ flexShrink: 0 }}>
                            .
                    </audio>
                </div>
            )}          

        </div>

        <Customize_Screen 
            screenCount={screenCount} 
            playerBoxes={playerBoxes} 
            positions={storedPositions[selectedScene - 1] || []} 
            colors={colors} 
            onSavePositions={handleSavePositions}
            onDanceFormation={StoredDanceFormation}
            onShowPopup={showPopup}
            onSceneTimes={setSceneTimes}
            onMovingTimes={setMovingTimes}
        />

        <div className="Customize-Map-board" onDragOver={handleDragOver}>
          {RenderBoard(8, 12)}
          {playerBoxes.length > 0 && (
              <CustomizeBoardCircle
                  Player_array={playerBoxes}
                  positions={storedPositions[selectedScene - 1] || []}
                  updatePosition={DragupdatePosition}
              />
          )}
        </div>

      </div>

      {showPopup && (
          <div className="Customize-popup">
              <div className='Customize-popup-title'>
                  <span className='Banner-title-text'>{Name}</span>

                <Close className='Customize-popup-title-close' onClick={handleClosePopup}/>
              </div>

                <div className='Customize-popup-board'>
                    {RenderBoard_popup(8, 12)}
                    {DanceFormation != [] && (
                        <Screen
                            Positions={DanceFormation}
                            MakeupTime={makeuptime}
                            BetweenTime={betweenTime}
                            HandleScreen={handleScreen}
                            />
                    )}
                </div>



              <div className='Customize-popup-handle'>
                <span className={`Customize_popup_Menu_icon_box ${selectedIcon === 'Play' ? 'active' : ''}`} onClick={() => handleClickScreen('Play')}><Play /></span>
                <span className={`Customize_popup_Menu_icon_box ${selectedIcon === 'Pause' ? 'active' : ''}`} onClick={() => handleClickScreen('Pause')}><Pause /></span>
                <span className={`Customize_popup_Menu_icon_box ${selectedIcon === 'Stop' ? '' : ''}`} onClick={() => handleClickScreen('Stop')}><Stop /></span>
              </div>


              <audio ref={audioRef} src={audioUrl} preload="auto" />

              <div className='Customize-popup-handle'>
                  <span
                      className={`Customize_popup_Menu_icon_box ${selectedIcon === 'Play' ? 'active' : ''}`}
                      onClick={() => handleClickScreen('Play')}
                  >
                      <Play />
                  </span>
                  <span
                      className={`Customize_popup_Menu_icon_box ${selectedIcon === 'Pause' ? 'active' : ''}`}
                      onClick={() => handleClickScreen('Pause')}
                  >
                      <Pause />
                  </span>
                  <span
                      className={`Customize_popup_Menu_icon_box ${selectedIcon === 'Stop' ? '' : ''}`}
                      onClick={() => handleClickScreen('Stop')}
                  >
                      <Stop />
                  </span>
              </div>

          </div>
        )}
    </div>
    
  );
}

export default Customize;
